
.Secao2 {
    display: flex;
    justify-content: space-between;
    height: 48.4rem;
    width: 100vw;
    box-shadow: 
    inset -50px -50px 50px rgba(255, 255, 255, 0.01),
    inset 50px 50px 50px rgba(0,0,0,0.2);
    background-color: #0C356A;
    border-top: #279EFF 9px solid;
    transition: all 1s;
    flex-wrap: wrap;
}

.Secao2.dark {
    border-top: var(--secondary-color) 8px solid;
    background-color: var(--dark-color);
}

.Secao2.dark .SobreMim h1 {
    border-color: var(--primary-color);
    color: var(--font-color);
}

.Secao2.dark .sobre-img img {
    border-color: var(--primary-color)
}

.SobreMim {
    display: flex;
    color: #fff;
    margin: 0% 5% 0% 0%;
    flex-flow: column;
}

.SobreMim h1 {
    display: flex;
    justify-content: right;
    border-bottom: #279EFF 7px solid;
    margin: 10% 0% 5% 0.845em;
    font-size: 1.275rem;
    font-family: 'Exo', sans-serif;
    transition: all 1s;
    letter-spacing: 2px;
    border-radius: 1%;
    max-width: 14.875rem;
}

.Secao2:hover h1 {
    max-width: 50%;
    justify-content: right;
}

.SobreMim p {
    font-family: 'Montserrat', sans-serif;
    justify-content: center;
    margin-left: 1rem;
    font-size: 1.2rem;
    max-width: 50vw;
}

.sobre-img {
    margin-top: 6.045rem;
    margin-right: 6rem;
}

.sobre-img  img{
    width: 20.355rem;
    border-radius: 45%;
    border: #279EFF solid 3px;
    animation: float 4s infinite;
    transition: all 0.9 ease-in-out 0s;
}

@keyframes float {
    0% , 100% {
        transform: translateY(4%);
    }
    50% {
        transform: translateY(-4%);
    }

}

@media (max-width: 1040px) and (min-width: 451px) {


    .Secao2:hover h1 {
        max-width: 20.875rem;
    }

    .SobreMim {
       display: flex;
    }
    
    .SobreMim p {
        font-size: 0.875rem;
        text-align: center;
        align-items: center;
        max-width: none;
    }

    .SobreMim h1 {
        font-size: 1.175rem;
        text-wrap: balance;
        border-bottom: px solid #279EFF;
    }

    .sobre-img {
        margin-top: 2.045rem;
        margin-left: 13rem;
        justify-content: center;
    }

    .sobre-img img {
        width: 0;
        border: none;
    }

}